@font-face {
  font-family: "sans-serif";
  src: url("../fonts/arialregular.woff2") format("woff2"), url("../fonts/arialregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "sans-serif";
  src: url("../fonts/arialbold.woff2") format("woff2"), url("../fonts/arialbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-family: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}
a {
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
}
.header__list {
  display: flex;
  list-style: none;
  padding-left: 0;
}
.site-nav__item {
  margin-left: 50px;
  position: relative;
}
.site-nav__link {
  font-size: 14px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}
.site-nav__item {
  position: relative;
}
.site-nav__item::before {
  content: "";
  opacity: 0;
  width: 100%;
  height: 3px;
  background-color: #2aacc8;
  position: absolute;
  top: -3px;
  left: 0;
  transition: opacity 0.2s linear;
}
.site-nav__item:hover::before {
  opacity: 1;
}
.submenu--select {
  position: relative;
}
.submenu--select::after {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  border-top: 2px solid #363636;
  border-right: 2px solid #363636;
  position: absolute;
  bottom: 9px;
  right: -14px;
  transform: rotate(135deg);
}
.submenu--select:hover:after {
  transform: rotate(-45deg);
}
.portfolio-inner__list {
  list-style: none;
  position: absolute;
  padding-left: 0;
  background-color: #e8ebf1;
  width: 100px;
  opacity: 0;
  transition: opacity 0.2s linear;
}
.portfolio-inner__item {
  padding: 5px 10px;
}
.portfolio-inner__link {
  font-size: 14px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}
.site-nav__item:hover .portfolio-inner__list {
  opacity: 1;
}
.footer {
  color: #363636;
}
.footer a:not(.social-desc) {
  color: #363636;
  font-size: 14px;
  font-weight: 400;
}
.footer ul {
  list-style: none;
  padding-left: 0;
}
.footer__nav {
  display: flex;
  justify-content: space-between;
}
.footer__about {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
}
.footer__about-title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}
.footer__about-subtitle {
  font-size: 14px;
  font-weight: 400;
  width: 240px;
  margin-top: 0;
}
.footer__explore {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
}
.footer__explore-title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}
.footer__explore-list {
  padding: 0;
  margin-top: 0;
}
.footer__browse {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
}
.footer__browse-title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}
.footer__browse-list {
  padding: 0;
  margin-top: 0;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
}
.footer__contacts-title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}
.footer__contacts-adress {
  width: 120px;
  margin: 0 0 10px;
}
.footer__contacts-adress span {
  font-weight: 700;
}
.footer__contacts-phone {
  width: 145px;
  margin: 0;
}
.footer__social {
  display: flex;
  flex-direction: column;
  flex-basis: 200px;
  padding-top: 30px;
}
.footer__social-title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}
.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0;
}
.social--facebook {
  position: relative;
}
.social--facebook::before {
  content: "";
  background-image: url(../../img/social/facebook.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}
.social--dreeble {
  position: relative;
}
.social--dreeble::before {
  content: "";
  background-image: url(../../img/social/dribbble.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 15px;
  cursor: pointer;
}
.social--pinterest {
  position: relative;
}
.social--pinterest::before {
  content: "";
  background-image: url(../../img/social/pinterest.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 0;
  right: -33px;
  cursor: pointer;
}
.social--link {
  position: relative;
}
.social--link::before {
  content: "";
  background-image: url(../../img/social/linkedin.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 45px;
  left: 0;
  cursor: pointer;
}
.social--skype {
  position: relative;
}
.social--skype::before {
  content: "";
  background-image: url(../../img/social/skype.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 45px;
  left: 35px;
  cursor: pointer;
}
.social--share {
  position: relative;
}
.social--share::before {
  content: "";
  background-image: url(../../img/social/share.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 55px;
  height: 50px;
  position: absolute;
  top: 45px;
  right: -70px;
  cursor: pointer;
}
.social-desc {
  font-size: 9px;
}
.footer__copy {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__copy-copyright {
  width: 650px;
  padding-bottom: 30px;
}
.footer__copy-logo {
  width: 133px;
  height: 38px;
  padding-bottom: 30px;
}
.portfolio__title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  padding-left: 40px;
  position: relative;
}
.portfolio__title::before {
  content: "";
  background-image: url(../../img/brush.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  top: -5px;
  left: 0;
  position: absolute;
  z-index: 10;
}
.portfolio__nav-list {
  list-style: none;
  display: flex;
  padding-left: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 1px solid #e8ebf1;
  border-bottom: 1px solid #e8ebf1;
}
.portfolio__nav-item {
  padding: 5px 10px;
}
.portfolio__nav-item:hover {
  background-color: #81e4ed;
}
.portfolio__nav-link {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.pagination {
  list-style: none;
  display: flex;
  justify-content: center;
  padding-left: 0;
}
.pagination__item {
  padding: 5px 10px;
}
.pagination__item:hover {
  background-color: #81e4ed;
}
.pagination__link {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.pagination__item-next {
  background-color: #e8ebf1;
  padding: 0;
  margin-right: 10px;
  width: 32px;
  position: relative;
}
.pagination__item-next::after {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  border-top: 2px solid #2aacc8;
  border-right: 2px solid #2aacc8;
  position: absolute;
  bottom: 13px;
  left: 12px;
  transform: rotate(45deg);
}
.pagination__item-next:hover {
  background-color: #c2c3c6;
}
.pagination__item-end {
  background-color: #e8ebf1;
  padding: 0;
  width: 32px;
  position: relative;
}
.pagination__item-end::before {
  content: "";
  background-image: url(../../img/arrow.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 12px;
  left: 10px;
}
.pagination__item-end:hover {
  background-color: #c2c3c6;
}
.pagination__end {
  font-size: 8px;
}
.pagination__next {
  font-size: 8px;
}
.visually-hidden {
  visibility: hidden;
}
.container {
  width: 1000px;
  margin: 0 auto;
  padding: 0 5px;
}
.projects {
  list-style: none;
  padding: 0;
  border-bottom: 1px solid #e8ebf1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.projects__item {
  margin-bottom: 40px;
}
.projects__img {
  width: 220px;
  height: 160px;
  cursor: pointer;
  transition: transform 0.2s linear;
}
.projects__img:hover {
  transform: translate3d(-10px, -10px, 0);
}
.header__wrapper {
  background-color: #e8ebf1;
  min-height: 120px;
}
.footer-wrapper {
  background-color: #e8ebf1;
  min-height: 120px;
}
h1 {
  margin: 0;
}
.contact__flex-wrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 40px;
}
.form__title {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  padding-left: 40px;
  padding-bottom: 15px;
  border-bottom: 1px solid #e8ebf1;
  position: relative;
}
.form__title::before {
  content: "";
  background-image: url(../../img/email.png);
  background-position: 3px 6px;
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  top: -5px;
  left: 0;
  position: absolute;
  z-index: 15;
}
.form-wrapper {
  display: flex;
  flex-direction: column;
}
.form__block {
  display: flex;
  flex-direction: column;
}
.form__block label {
  margin-top: 15px;
  margin-bottom: 3px;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.form__desc {
  max-width: 575px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.form__email {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.form__email span {
  color: #2aacc8;
  cursor: pointer;
}
.button-contacts-page {
  font-size: 15px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  align-self: flex-start;
  min-width: 160px;
  min-height: 60px;
  margin-top: 20px;
  padding: 0 0 0 35px;
  background: linear-gradient(to bottom, #7fe3ec 5%, #39b5ce 100%);
  box-shadow: inset 0px 1px 0px #94f7ff;
  border: solid 1px #2aacc8;
  border-radius: 8px;
  box-shadow: 0 3px 4px #d2d2d2;
  position: relative;
  cursor: pointer;
}
.button-contacts-page::before {
  content: "";
  background-image: url(../../img/btn-arrow.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  top: 16px;
  left: 25px;
  display: block;
  position: absolute;
  z-index: 200;
}
.map-wrapper {
  display: flex;
  flex-direction: column;
}
.map__background {
  background-color: #e8fcfe;
  border: solid 1px #81e4ed;
  width: 340px;
  height: 280px;
}
.map__desc {
  font-weight: 700;
  font-size: 14px;
  padding-left: 20px;
}
.map__block {
  margin-left: 20px;
  border: solid 5px #ffffff;
}
.map__contacts-title {
  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 20px;
}
.map__contacts-address {
  width: 120px;
  margin: 0 0 10px;
}
.map__contacts-address span {
  font-weight: 700;
}
.map__contacts-phone {
  width: 145px;
  margin: 0;
}
